.green-gradient {
    background: green; /* For browsers that do not support gradients */
    background: -webkit-linear-gradient(right bottom, mediumseagreen, #09edb7); /* For Safari 5.1 to 6.0 */
    background: -o-linear-gradient(top left, mediumseagreen, #09edb7); /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
    background: -webkit-gradient(linear, right bottom, left top, from(mediumseagreen), to(#09edb7));
    background: -webkit-linear-gradient(bottom right, mediumseagreen, #09edb7);
    background: -o-linear-gradient(bottom right, mediumseagreen, #09edb7);
    background: linear-gradient(to top left, mediumseagreen, #09edb7); /* Standard syntax (must be last) */
}

